.swap {
	.label {
		margin: 5px 0;
	}

	.asset {
		position: relative;

		label.asset {
			background: rgb(43, 47, 54);
			padding: 10px;
			border-radius: 5px;
			display: flex;
			justify-content: space-between;
		}

		div.list {
			display: none;

			position: absolute;
			z-index: 999999999;
			left: 0px;
			right: 0px;
			border-bottom-left-radius: 5px;
			border-bottom-right-radius: 5px;
			background-color: #181a19;
			margin-bottom: 5px;
			margin-top: 0px;
			box-shadow: 0 7px 7px 1px #1e2026;

			.search {
				display: flex;
				background-color: #2b2f36;
				padding: 5px 10px;

				input {
					width: 100%;
					border: none;
					outline: none;
					background-color: transparent;
					color: white;
					padding: 5px;
				}
			}

			ul {
				list-style: none;
				margin: 0;
				padding: 0 0 5px 0;

				li {
					display: flex;
					align-items: center;
					padding: 5px 10px;
					white-space: nowrap;
					cursor: pointer;
					border-top: 1px solid #1e2026;

					&:not(.disabled):hover {
						background-color: #111;
					}

					&.disabled {
						cursor: auto;

						img {
							opacity: 0.3;
						}

						span {
							color: #888;
						}
					}


					span {
						width: 3em;
					}

					small {
						margin-left: 20px;
						color: #888
					}
				}
			}
		}

		label.overlay {
			display: none;
			position: fixed;
			z-index: 100;
			color: black;
			opacity: 0.5;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
		}

		input:checked~div.list {
			display: block;
		}

		input:checked~label.asset {
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;
		}

		input:checked~label.overlay {
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;
			border-bottom: 1px soild #222;
			display: block;
		}

	}

	.chain-font {
		font-weight: bold;
		opacity: 0.5;
		font-size: 13x;
	}

	.switch {
		display: flex;
		padding: 10px;

		&:hover {
			box-shadow: 0 0 8px 3px #ffff9ba6;

			svg {
				fill: black;
			}
		}
	}

	.chain {
		position: relative;
		box-shadow: rgb(11, 14, 17) 0px 2px 8px;
		padding: 1em;
		flex: 1 1 0%;
		display: flex;
		flex-direction: column;
		-webkit-box-pack: justify;
		justify-content: space-between;
		border-radius: 0.5em;

		img.icon {
			width: 64px;
			height: 64px;
		}

		div.icon {
			width: 64px;
			height: 64px;
			border: 3px solid #1d3345;
			border-radius: 50%;
			padding: 10px;

			&.yellow {
				border: 3px solid #52402c;
			}

			img {
				width: 100%;
				height: 100%;
			}
		}

		div.menu {
			i {
				display: flex;
				padding: 5px;
				border-radius: 5px;
				background-color: #2b2f36;
				transition: all 0.5s ease;
			}

			ul {
				display: none;
				z-index: 999999999;
				min-width: 200px;
				margin-top: 0;
				margin-bottom: 0;
				padding-top: 5px;
				padding-bottom: 5px;
				list-style: none;
				padding-left: 0;

				position: absolute;
				left: 0;
				background-color: #14151a;
				border-radius: 5px;

				&.right {
					left: auto;
					right: 0;
				}

				li {
					display: flex;
					padding: 10px 20px;
					white-space: nowrap;
					cursor: pointer;
					border-bottom: 1px solid #222;

					&:not(.disabled):hover {
						background-color: #111;
					}

					&.disabled {
						cursor: auto;

						img {
							opacity: 0.3;
						}

						span {
							color: #888;
						}
					}
				}

				img.icon {
					width: 20px;
					height: 20px;
					margin-right: 10px;
				}
			}

			&:hover {
				i {
					background-color: #f8d12f;
					color: black;

					svg {
						fill: black;
					}
				}

				ul {
					display: block;
				}
			}
		}

	}

